@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#1d1d2c;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  text-align: center;
}
h1{
  color:#f7f4e9;
  font-weight: 900;
  font-size: 3em;
  font-family: 'Fjalla One', sans-serif;
}
.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
}
.elem {
  display: flex;
  justify-content: space-between;
  background: #f7f4e9;
  margin-bottom: 1%;
  width: 40vw;
  padding: 2%;
  height: 4vh;
  align-items: center;
}
.text {
  display: flex;
  justify-content:flex-start;
  align-items: center;
  width: 50%;
  font-size: 1.2em;
  font-weight: 500;
}
.btn-delete {
  color: #000000;
  width: 15% !important;
  transition: 0.3s;
  cursor: pointer;
}
.btn-delete:hover {
  color: #e70e2e;
  font-size: 1.2em;
  transition: 0.3s;
}
.checked:hover{
  color: #438945;
}

.todo-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.add-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vh;
  width: 70vw;
}
.add-content {
  width: 50vw;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input {
  background-color: #f7f4e9;
  width: 30vw;
  padding: 3%;
}
.list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
ol {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 68vw;
    position: relative;
    padding: 0px;
    list-style: none;
    overflow-y: scroll;
    height: 56vh;
    scrollbar-color: black white;
}
.btn-add {
  padding: 3%;
  width: 4vw !important;
  background-color: #438945;
  border: none;
  transition: 0.3s;
  cursor: pointer;
  color: white;
  font-size: 1.4em;
}

.btn-add:hover {
  height: 5vh;
  background-color: #5ebd61;
  border: none;
  transition: 0.3s;
}
.btn-return{
    background-color:#3cbcc3;
    border: none;
    padding: 1.5%;
    width: 10vw !important;
    cursor: pointer;
}
.btn-return:hover{
    background-color:#45dce4;
    border: none;
    padding: 1.5%;
    width: 10vw !important;
}
.no-task {
  height: 40vh;
  width: 40vw;
  font-size: 1.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #eba63f;
  -webkit-animation: pulse 2s ease infinite;
          animation: pulse 2s ease infinite;
}
@-webkit-keyframes pulse {
  0% {
    font-size: 1.7em;
  }
  50% {
    font-size: 2em;
  }
  100% {
    font-size: 1.7em;
  }
}
@keyframes pulse {
  0% {
    font-size: 1.7em;
  }
  50% {
    font-size: 2em;
  }
  100% {
    font-size: 1.7em;
  }
}

.error {
  height: 40vh;
  width: 40vw;
  font-size: 1.7em;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #e70e2e;
  -webkit-animation: pulse 2s ease infinite;
          animation: pulse 2s ease infinite;
}
@keyframes pulse {
  0% {
    font-size: 1.7em;
  }
  50% {
    font-size: 2em;
  }
  100% {
    font-size: 1.7em;
  }
}

